/* src/App.css */
body {
  background-color: black;
  background: url("../public/Фон.png") repeat; /* Бесконечный фон */
}
.logo {
  height: 250px; /* Высота логотипа */
  padding: 20px;
  margin: 20px;
}

.participant-list, .header {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 20px;
  color: white;
}

.list-item {
  background: rgba(165, 165, 165, 0.6);
  padding: 10px;
  margin: 5px;
  border-radius: 10px;
  width: 80%;
  display: flex;
  justify-content: space-between;
  font-size: 50px;
}

.header-items{
  padding: 10px;
  margin: 5px;
  border-radius: 10px;
  width: 80%;
  display: flex;
  justify-content: space-around;
}

.header-text{
  font-size: 125px;
}

a{
  margin-left: 5%;
  margin-right: 5%;
}

.header{
  display: flex;
  padding: 0;
}